import { Component, OnInit } from '@angular/core';
import { CartService } from '../core/cart.service';
import { UtilityService } from '../core/utility.service';

@Component({
  selector: 'app-payment-methode',
  templateUrl: './payment-methode.page.html',
  styleUrls: ['./payment-methode.page.scss'],
})
export class PaymentMethodePage implements OnInit {
  hideMe: any;
  total;
  constructor(
    private utility: UtilityService,
    private cartService: CartService
  ) {}

  ngOnInit() {
    this.getTotal();
  }
  hide() {
    this.hideMe = !this.hideMe;
  }

  getTotal() {
    this.total = this.utility.getLocal('total');
  }

  placeOrder() {
    this.utility.present();
    let data = JSON.parse(this.utility.getLocal('cartDetail'));
    data = data.map((v) => {
      let obj = { productId: v.productId._id, qty: v.qty, price: v.price };
      return obj;
    });
    let json = {
      user: this.utility.getUserId(),
      items: data,
      totalPrice: this.total,
      address: 'my address',
      paymentId: new Date().getTime(),
    };
    this.cartService.saveOrder(json).subscribe((res) => {
      console.log(res);
      this.utility.dismiss();

      this.utility.navigate('/payment-successful');

      this.cartService
        .userCartClear(this.utility.getUserId())
        .subscribe((resp) => {
          console.log(resp);
        });
    });
  }

  goBack() {
    this.utility.goBack();
  }
}
